import {FBFieldBase} from "../../Fields/Core/FBFieldBase";
import {ShowHideConditionOptions} from "../../Fields/Core/ConditionOptions";
import {ConditionProcessorBase} from "./ConditionProcessorBase";
import {FBFieldBaseModel} from "FormBuilder/Fields/Core/FBFieldBase.Model";
import {ContainerDataRetriever} from "FormBuilder/Core/FormBuilder.Model";

export class ShowHideConditionProcessor  extends ConditionProcessorBase{
    public Options:ShowHideConditionOptions;
    public Field:FBFieldBaseModel;


    public Initialize(formBuilder: ContainerDataRetriever, condition: ShowHideConditionOptions): this {
        throw Error("Invalid initialization, please use initialize with fields instead");
    }

    InitializeWidthFields(field: FBFieldBaseModel, condition: ShowHideConditionOptions): this {
        this.Field=field;
        return super.Initialize(field.FormBuilder, condition);
    }

    ExecuteFalseAction(immediate:boolean) {
        if(this.Options.ShowWhenTrue)
            this.Field.Hide(immediate);
        else
            this.Field.Show(immediate);
    }

    ExecuteTruAction(immediate:boolean) {
        if(this.Options.ShowWhenTrue)
            this.Field.Show(immediate);
        else
            this.Field.Hide(immediate);
    }




}