import {FormBuilderOptions} from "FormBuilder/Core/FormBuilder.options";
import {FBRowModel} from "FormBuilder/Core/FBRow.Model";
import {FBFieldBaseOptions, FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";
import {FBRowOptions} from "FormBuilder/Core/FBRow.Options";
import {FBColumnOptions} from "FormBuilder/Core/FBColumn.Options";

import {FieldAdded} from "ProductFieldBuilder/Events/FieldAdded";
import {FBColumnModel} from "FormBuilder/Core/FBColumn.Model";
import {FBFieldBaseModel} from "FormBuilder/Fields/Core/FBFieldBase.Model";
import {FBFieldBase} from "FormBuilder/Fields/Core/FBFieldBase";

import {PriceManager} from "FormBuilder/Utilities/Managers/PriceManager/PriceManager";

import {FBRow} from "FormBuilder/Core/FBRow";
import {ModelWithParent} from "FormBuilder/Core/Models/ModelWithParenth";
import {FBGroupPanelModel} from "../../Pro/Fields/Fields/FBGroupPanel/FBGroupPanel.Model";
import {FieldFactory} from "FormBuilder/FieldFactory";
import {ProductData} from "FormBuilder/Core/FormBuilder";
import {ModelSerialize} from "FormBuilder/Core/Models/ModelBase";
import * as React from "react";
import {FBFieldWithPriceBaseModel} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Model";
import {SingleEvent} from "shared/core/Events/SingleEvent";
import {PublishOnceEvent} from "shared/core/Events/PublishOnceEvent";
import {EventManager} from "shared/core/Events/EventManager";
import {LoadExtensionOptions} from "FormBuilder/Events/LoadExtensionOptions";

export class FormBuilderModel extends ModelWithParent<FormBuilderOptions,any> implements ContainerDataRetriever{
    @ModelSerialize()
    public Rows:FBRowModel[];
    LastFieldId:number;
    public Parent:any;

    FieldValueChanged: SingleEvent<FBFieldBaseModel>;
    IsField: boolean;
    IsLoaded: boolean;
    OnLoad: PublishOnceEvent;

    PriceManager: PriceManager;
    ProductPriceChanged: SingleEvent;

    VariationChanged: SingleEvent;


    public IsDesign():boolean{
        return false;
    }

    constructor(Options: FormBuilderOptions, Parent: any,public Product:ProductData,public PreviousData:any=null) {
        super(Options, Parent);

        this.ProductPriceChanged=new SingleEvent();
        this.PriceManager=new PriceManager(this);
        this.LastFieldId=0;
        this.Rows=[];
        this.VariationChanged=new SingleEvent();
        this.FieldValueChanged=new SingleEvent();
        this.IsField=false;


        this.OnLoad=new PublishOnceEvent();
        this.IsLoaded=false;
        for(let row of Options.Rows)
        {
            this.Rows.push(new FBRowModel(row,this))
        }

        this.SetLastFieldId(this.Rows);
    }

    public Initialize(){
        this.GetFields().forEach(x=>x.InitializeConditions());
        EventManager.Publish(new LoadExtensionOptions(this));
        return this;
    }



    public ExecuteFirstCalculation(){
        this.GetFields().forEach(field=>{
            if(field instanceof FBFieldWithPriceBaseModel)
            {
                if((field as any).ExecuteFirstCalculation!=null)
                    (field as any).ExecuteFirstCalculation();


                let fieldCalculator=field as FBFieldWithPriceBaseModel;
                if(!fieldCalculator.calculator.GetDependsOnOtherFields())
                {
                    fieldCalculator.calculator.ExecuteAndUpdate();
                }

                if(fieldCalculator.calculator.GetDependsOnOtherFields())
                {
                    fieldCalculator.calculator.ExecuteAndUpdate();
                }
            }
        })
    }

    public AddField(field:FBFieldBaseOptions,index:number)
    {
        this.LastFieldId++;
        field.Id=this.LastFieldId;



        let row=new FBRowModel(new FBRowOptions().Merge(),this);
        let colOptions=new FBColumnOptions().Merge();
        colOptions.Field=field;
        let columnModel=new FBColumnModel(colOptions,row);
        row.Columns.push(columnModel);

        columnModel.Field=FieldFactory.GetFieldModel(columnModel,field);
        this.Rows.splice(index,0,row);

        this.Refresh();

    }

    public CreateRow(options:FBRowModel,index:number)
    {
        return <FBRow key={options.Columns[0].Field.Options.Id} Model={options}/>;
    }


    public CreateSubRow(options:FBRowModel)
    {
        return <FBRow  key={options.Columns[0].Field.Options.Id} Model={options}/>;
    }

    DeleteField(field: FBFieldBaseModel) {
        let row=FBRowOptions;
        for(let currentRow of this.Rows)
        {
            if(currentRow.Columns.find(x=>x.Field==field)!=null)
            {
                this.Rows.splice(this.Rows.indexOf(currentRow),1);
                this.Refresh();
            }
        }
    }

    GetFieldById(id: any):FBFieldBaseModel {
        for(let field of this.GetFields())
            if(field.Options.Id==id)
                return field;

        return null;
    }

    GetFields(): FBFieldBaseModel[] {
        let fields:FBFieldBaseModel[]=[];
        this.Rows.map(row=>row.Columns.map(column=>fields.push(column.Field)));
        return fields;
    }




    GetNextFieldId(): number {

        this.LastFieldId++;
        return this.LastFieldId;
    }


    public GetProductPrice(){
        if(ProductBuilderOptions.IsVariable=='1')
        {
            let selectedVariable=this.GetSelectedVariable();
            if(selectedVariable==null)
                return 0;
            return selectedVariable.price;
        }
        return this.Product.Price;
    }


    public get FormBuilder():ContainerDataRetriever{
        return null;
    }



    GetRowByFieldId(fieldId: number): FBRowModel {
        return this.InternalGetRowdById(fieldId,this);

    }

    private InternalGetRowdById(fieldId:number,container:ContainerDataRetriever): FBRowModel {
        for(let row of container.GetRows())
        {
            if(row.Columns[0].Field.Options.Id==fieldId)
                return row;

            if(row.Columns[0].Field.Options.IsFieldContainer)
            {
                let rowReturned=this.InternalGetRowdById(fieldId,row.Columns[0].Field as FBGroupPanelModel);
                if(rowReturned!=null)
                    return rowReturned;
            }

        }

        return null;
    }


    private InternalGetFieldById(fieldId:number,container:ContainerDataRetriever): FBRowModel {
        for(let row of container.GetRows())
        {
            if(row.Columns[0].Field.Options.Id==fieldId)
                return row;

            if(row.Columns[0].Field.Options.IsFieldContainer)
            {
                let rowReturned=this.InternalGetFieldById(fieldId,row.Columns[0].Field as FBGroupPanelModel);
                if(rowReturned!=null)
                    return rowReturned;
            }

        }

        return null;
    }

    GetRows(): FBRowModel[] {
        return this.Rows;
    }

    GetRuler(): any {
    }



    public GetSalePrice(){
        if(ProductBuilderOptions.IsVariable=='1')
        {
            let selectedVariable=this.GetSelectedVariable();
            if(selectedVariable==null)
                return 0;
            return selectedVariable.sale_price;
        }
        return this.Product.SalePrice;
    }

    public GetSelectedVariable(){
        let variation=document.getElementsByName('variation_id');
        if(variation.length==0)
            return null;

        let variationId=(variation[0] as HTMLInputElement).value;
        return ProductBuilderOptions.Variations.Variations.find(x=>x.variation_id==variationId);

    }

    GetVariationValue(VariationName: string) {
        return (document.querySelector('#'+VariationName.toLowerCase()) as HTMLSelectElement).value;
    }


    private SetLastFieldId(Rows: FBRowModel[]) {
        for(let currentRow of Rows)
            for(let currentColumn of currentRow.Columns)
            {
                this.LastFieldId=Math.max(this.LastFieldId,currentColumn.Field.Options.Id);

                if(currentColumn.Field.Options.Type==FieldTypeEnum.Repeater||currentColumn.Field.Options.Type==FieldTypeEnum.GroupPanel)
                    this.SetLastFieldId((currentColumn.Field as any).Rows);
            }

    }

    GetId(): string {
        return null;
    }

    GetPluginURL(): any {
        return ProductBuilderOptions.URL;
    }

}



export interface ContainerDataRetriever {
    PreviousData:any;
    GetRows():FBRowModel[];
    GetFields():FBFieldBaseModel[];
    GetRowByFieldId(fieldId:number):FBRowModel;
    FormBuilder:ContainerDataRetriever;
    IsLoaded:boolean;
    OnLoad:PublishOnceEvent;
    PriceManager:PriceManager;
    GetFieldById(id:any):FBFieldBaseModel;
    GetVariationValue(VariationName:string);
    VariationChanged:SingleEvent;
    ProductPriceChanged:SingleEvent;
    GetProductPrice():any;
    GetSalePrice():any;
    Refresh();
    GetRuler():any;
    DeleteField(field:FBFieldBaseModel);
    IsField:boolean;
    GetNextFieldId():number;
    CreateSubRow(options:FBRowModel);
    FieldValueChanged:SingleEvent<FBFieldBaseModel>;
    ExecuteFirstCalculation();
    GetId(): string;
    GetPluginURL(): any;
}


declare let ProductBuilderOptions:{
    Options:FormBuilderOptions,
    WCCurrency:any,
    IsVariable:string;
    Product:ProductData;
    PreviousData:any[];
    URL:string;
    Variations:{
        Attributes:any;
        Variations:{
            price:string;
            variation_id:string;
            sale_price:string;
        }[]
    }
};