import {FieldTypeEnum} from "../../../Fields/Core/FBFieldBase.options";
import {SingleValueRetriever} from "../SingleValueRetriever";
import {MultipleValueRetriever} from "../MultipleValueRetriever";
import {NoneValueRetriever} from "../NoneValueRetriever";
import {FBFieldBaseModel} from "FormBuilder/Fields/Core/FBFieldBase.Model";
import {NumericValueRetriever} from "FormBuilder/Core/ValueRetriever/NumericValueRetriever";


export class ValueRetrieverFactory {
    public static GetValueRetriever(field:FBFieldBaseModel){
        switch (field.Options.Type) {
            case FieldTypeEnum.Text:
            case FieldTypeEnum.TextArea:
                return new SingleValueRetriever(field);
            case FieldTypeEnum.Radio:
            case FieldTypeEnum.DropDown:
            case FieldTypeEnum.Checkbox:
                return new MultipleValueRetriever(field);
            case FieldTypeEnum.Datepicker:
                return new NumericValueRetriever(field);

        }

        return new NoneValueRetriever(field);
    }
}