
import {ConditionOptionsBase, ConditionTypeEnum, ShowHideConditionOptions} from "./ConditionOptions";
import {StoreBase, StoreDataType} from "shared/core/Store/StoreBase";

export enum FieldTypeEnum {
    Text='text',
    TextArea='textarea',
    Radio='radio',
    Checkbox='checkbox',
    DropDown='dropdown',
    Hidden='hidden',
    Paragraph='paragraph',
    FileUpload='fileupload',
    ImagePicker='imagepicker',
    Divider='divider',
    Repeater='repeater',
    List='list',
    GroupPanel='grouppanel',
    RepeaterItem='repeater_item',
    Datepicker='datepicker',
    DateRange='daterange',
    Masked='masked',
    ColorPicker='colorpicker'
}



export class FBFieldBaseOptions extends StoreBase{
    Id:number;
    Type:FieldTypeEnum;
    Label:string;
    Required:boolean;
    RowId:number;
    @StoreDataType(ConditionOptionsBase)
    Conditions:ConditionOptionsBase[];
    public IsFieldContainer:boolean;
    protected LoadDefaultValues() {
        this.Type=FieldTypeEnum.Text;
        this.Label='';
        this.Id=0;
        this.Required=false;
        this.RowId=0;
        this.Conditions=[];
    }


    protected GetValueFromLoader(property:any, value: any): any {
        if(property=="Conditions")
        {
            if(value==null)
                return [];

            let valuesToReturn=[];
            for(let currentValue of value as ConditionOptionsBase[])
            {
                switch (currentValue.Type) {
                    case ConditionTypeEnum.ShowHide:
                        valuesToReturn.push(
                            new ShowHideConditionOptions().Merge(currentValue)
                        );
                }
            }

            return valuesToReturn;
        }
    }
}

