import * as React from "react";
import {FormBuilderOptions} from "./FormBuilder.options";
import { FBRow} from "./FBRow";
import {RefArray} from "shared/utilities/react/RefArray";
import {RowDesigner} from "../../ProductFieldBuilder/Designer/RowDesigner";
import {FBRowOptions} from "./FBRow.Options";
import {Price} from "../Utilities/Components/Price";
import {PriceManager} from "../Utilities/Managers/PriceManager/PriceManager";
import {FormBuilder} from "./FormBuilder";
import {ContainerDataRetriever} from "FormBuilder/Core/FormBuilder.Model";


export class TotalContainer extends React.Component<TotalContainerProps, TotalContainerState> {

    constructor(props: TotalContainerProps) {
        super(props);
        this.state = {};
        props.FormBuilder.PriceManager.TotalUpdated.Subscribe(this,x=>this.forceUpdate());
    }


    public render() {
       return (
           <div className={'rnTotalBox'}>
               <label>Options amount</label>
               <Price Amount={this.props.FormBuilder.PriceManager.GetOptionsTotal()}/>
               <label>Final total</label>
               <Price Amount={this.props.FormBuilder.PriceManager.GetGrandTotal()}/>
           </div>
       )
    }


}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
export interface TotalContainerProps {
    FormBuilder:ContainerDataRetriever;
}

export class TotalContainerState {

}


