
import {PriceTypeEnum} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {StoreBase} from "shared/core/Store/StoreBase";

export class OptionItem extends StoreBase{

    public Label:string;
    public RegularPrice:string;
    public SalePrice:string;
    public PriceType:PriceTypeEnum;
    public Id:number;
    public Selected:boolean;
    public URL:string;

    protected LoadDefaultValues() {
        this.Selected=false;
        this.Id=0;
        this.Label='';
        this.RegularPrice='';
        this.SalePrice='';
        this.PriceType=PriceTypeEnum.fixed_amount;
        this.URL='';
    }


}