import {ModelWithParent} from "FormBuilder/Core/Models/ModelWithParenth";
import {FBFieldWithPriceBaseOptions} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FBColumnModel} from "FormBuilder/Core/FBColumn.Model";
import {FBFieldBaseModel} from "FormBuilder/Fields/Core/FBFieldBase.Model";
import {FBFieldBaseOptions} from "FormBuilder/Fields/Core/FBFieldBase.options";
import {CalculatorBase} from "FormBuilder/Core/Calulators/CalculatorBase";
import {CalculatorFactory} from "FormBuilder/Core/Calulators/CalculatorFactory";

export abstract class FBFieldWithPriceBaseModel<Op extends FBFieldWithPriceBaseOptions=FBFieldWithPriceBaseOptions,P extends FBColumnModel=FBColumnModel> extends FBFieldBaseModel<Op,P>{
    calculator:CalculatorBase;
    Immediate:boolean;
    Quantity:any;

    constructor(Options: Op, Parent: P) {
        super(Options, Parent);
        this.Immediate=true;
        this.InitializePriceCalculator();
    }

    protected InternalSerialize(obj: any) {

        obj.PriceType=this.Options.PriceType;
        obj.Price=this.calculator.GetPrice();
        obj.SalePrice=this.calculator.GetSalePrice();
        obj.Quantity=this.calculator.GetQuantity();
        obj.UnitPrice=this.calculator.GetUnitPrice();
    }


    protected ExecuteInitialCalculation(){

    }


    public GetSalePrice(){
        return this.Options.SalePrice==null?"":this.Options.SalePrice.toString().trim();
    }

    public GetRegularPrice(){
        return this.Options.Price==null?"":this.Options.Price.toString().trim();
    }

    protected InitializePriceCalculator() {
        this.calculator=CalculatorFactory.GetCalculator(this.Options.PriceType);
        this.calculator.Initialize(this);
    }

    public abstract GetValue();
}