import * as React from "react";
import {FBColumn} from "./FBColumn";
import {FBRowModel} from "FormBuilder/Core/FBRow.Model";
import {FBColumnModel} from "FormBuilder/Core/FBColumn.Model";

export class FBRow extends React.Component<FBRowProps, FBRowState> {
    static defaultProps: Partial<FBRowProps> = {};
    public Instance:HTMLDivElement;
    public Column:FBColumn;
    constructor(props: FBRowProps) {
        super(props);
        this.state = {};
        this.RenderRefUpdated=this.RenderRefUpdated.bind(this);

    }

    public RenderRefUpdated(x:any)
    {
        if(x==null)
            return;
        this.props.Model.Instance=x;
    }


    public render() {

        return (
            <div ref={this.RenderRefUpdated} className={'rednaoWooRow'}>
                {this.props.Model.Columns.map(x=>
                    this.GetColumn(x.Field.Options.Id,x)
                )}
            </div>
        )
    }

    public GetColumn(key:any,options:FBColumnModel)
    {
        return <FBColumn   key={key} Model={options} />
    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
export interface FBRowProps {
    Model:FBRowModel;

}


export interface FBRowState {

}

//#endregion "My Region"

